
@font-face {
    font-family: 'Raleway';
    src: local('Raleway Light'), local('Raleway-Light'), url('../fonts/../fonts/ralewaylight.woff2') format('woff2'), url('../fonts/ralewaylight.woff') format('woff');
    font-weight: 300;
    font-style: normal; }

@font-face {
    font-family: 'Raleway';
    src: local('Raleway Regular'), local('Raleway-Regular'), url('../fonts/ralewayregular.woff2') format('woff2'), url('../fonts/ralewayregular.woff') format('woff');
    font-weight: 400;
    font-style: normal; }

@font-face {
    font-family: 'Raleway';
    src: local('Raleway Medium'), local('Raleway-Medium'), url('../fonts/ralewaymedium.woff2') format('woff2'), url('../fonts/ralewaymedium.woff') format('woff');
    font-weight: 500;
    font-style: normal; }

@font-face {
    font-family: 'Raleway';
    src: local('Raleway SemiBold'), local('Raleway-SemiBold'), url('../fonts/ralewaysemibold.woff2') format('woff2'), url('../fonts/ralewaysemibold.woff') format('woff');
    font-weight: 600;
    font-style: normal; }

@mixin base {
	font-weight: 400;
	font-family: $font-base; }

@mixin title {
	font-weight: 900;
	font-family: $font-title; }
